/*BS5 min*/
/*slogan*/
.slogan{
    max-height: 680px;
    padding: 77px 0 66px;
    .img-slogan{
        height: 535px;
        background-image: url(https://raw.githubusercontent.com/hexschool/2022-web-layout-training/main/week6-202207/bg_home.jpg);
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
    }
    .banner-slogan{
        background-color: #1C1C1C;
        padding: 47px 80px;
        border: 1px solid #FFFF;
    }
    .square{
        width: 10px;
        height: 10px;
        background-color: #ffff;
    }
}

@include mobile{
    .img-slogan{
        height: 336px;
        background-image: url(https://raw.githubusercontent.com/hexschool/2022-web-layout-training/main/week6-202207/bg_home_mobile.jpg);
    }
    .banner-slogan{
        padding: 32px 28px;
    }
}

/*publicity */
.publicity{
  padding: 80px 0;
  .box-publicity{
    border: 1px solid #2D2D2D;
    padding: 48px 0;
    border-radius: 12px;
    &:hover{
      background-color: #202020;
      border-color: $secondary;
      span{
        background: linear-gradient(#09ACF5, $secondary);
        background-clip: text;
        -webkit-background-clip: text;
        color: transparent;
      }
    }
  }
}
@include mobile{
  .publicity{
    padding: 40px 0;
    .title-line-style{
      margin: 0 auto;
    }
    .box-publicity{
      background-color: #202020;
      border-color: #202020;
      span{
        background: linear-gradient(#09ACF5, $secondary);
        background-clip: text;
        -webkit-background-clip: text;
        color: transparent;
      }
    }
  }
}

/*process */
.process{
    background-color: #1C1C1C;
    padding: 80px 0;
    .box-process{
      width: 100%;
      border: 1px solid #FFFFFF;
      border-radius: 12px;
      padding: 48px 0 48px 48px;
      z-index: 1;
    }
    .title-box-process{
      position: absolute;
      background-color: #1C1C1C;
      top: -28px;
      left: 48px;
      z-index: 2;
    }
}
@include mobile{
  .process{
    padding: 40px 0;
  }
}

/*program */
/*swiper-slide */
.program{
  padding: 80px 24px;
  .swiper-slide{
    width: auto; /**/
  }
  .slide-btn-style{
    border: 1px solid #ffff;
    padding: 24px 32px;
  }
}

/*recommend */
.recommend{
  padding: 80px 0;
  .card{
    border: 2px solid #2D2D2D;
    border-radius: 24px;
    &:hover{
      border: 2px solid $secondary;
    }
  }
  .shot-img{
    width: 100px;
    height: 100px;
    border-radius: 50%;
  }
  .card-body{
    padding: 24px 24px 32px;
  }
  .price-card{
    border-top: 1px solid #C1C1C1;
    padding: 16px 24px;
  }
  .btn-recommend-style{
    padding: 8px 32px;
  }
}
@include mobile{
  .recommend{
    padding: 40px 0;
    .card-body{
      padding: 24px 32px 16px;
    }
    .price-card{
      padding: 16px 32px;
    }
  }
}

/*Strategy */
.strategy{
  .box-strategy{
    position: relative;
    padding: 216px 0;
    color: #ffff;
    &::before{
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
      background-color: color-1;
      opacity: 0.6;  /*  1-0.6-0-*/
    }
    &:hover{
      &::before{ /*hover*/
        display: none;
      }
      .icon-strategy{
        color: $secondary;
      }
      .line-strategy{
        width: 70px;
        background-color: $secondary;
      }
    }
  }
  .line-strategy{
    width: 50px;
    height: 3px;
    background-color: #ffff;
  }
  .bg-img-1{
    background-image: url(https://raw.githubusercontent.com/hexschool/2022-web-layout-training/main/week6-202207/info01.jpg);
    background-position: center;
    background-size: cover; 
    background-repeat: no-repeat;
  }
  .bg-img-2{
    background-image: url(https://raw.githubusercontent.com/hexschool/2022-web-layout-training/main/week6-202207/info02.jpg);
    background-position: center;
    background-size: cover; 
    background-repeat: no-repeat;
  }
  .bg-img-3{
    background-image: url(https://raw.githubusercontent.com/hexschool/2022-web-layout-training/main/week6-202207/info03.jpg);
    background-position: center;
    background-size: cover; 
    background-repeat: no-repeat;
  }
  .bg-img-4{
    background-image: url(https://raw.githubusercontent.com/hexschool/2022-web-layout-training/main/week6-202207/info04.jpg);
    background-position: center;
    background-size: cover; 
    background-repeat: no-repeat;
  }
}
@include mobile{
  .strategy{
    .box-strategy{
      padding: 40px 0;
    }
  }
}

/*article */
.article{
  background-color: #1C1C1C;
  padding: 80px 0;
  .title-line-style{ /**/
    width: 50px;
    height: 3px;
    background-color: #62DB54;
  }
  .card{
    background-color: #1C1C1C;
    padding: 32px;
    &:hover{
      .article-cardRounded{
        display: block;
      }
    }
  }
  .article-cardRounded{
    display: none; /**/
    width: 32px;
    height: 32px;
    &-TR{
      border-top: 1px solid $secondary;
      border-right: 1px solid $secondary;
      border-top-right-radius: 12px;  /*TR*/
    }
    &-TL{
      border-top: 1px solid $secondary;
      border-left: 1px solid $secondary;
      border-top-left-radius: 12px;
    }
    &-BR{
      border-bottom: 1px solid $secondary;
      border-right: 1px solid $secondary;
      border-bottom-right-radius: 12px;
    }
    &-BL{
      border-bottom: 1px solid $secondary;
      border-left: 1px solid $secondary;
      border-bottom-left-radius: 12px;
    }
  }
}
@include mobile{
  .article{
    .card{
      padding: 0;
    }
  }
}

/*feedback */

.feedback{
  background-image: url(https://raw.githubusercontent.com/hexschool/2022-web-layout-training/main/week6-202207/bg_share.jpg);
  max-height: 733px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  padding: 80px 0;
  .card{
    width: 416px;
    height: 178px;
    padding: 32px;
    border-radius: 24px 24px 24px 0;
    z-index: 2;
    &-img{
      position: absolute;
      top: -52px;
      right: 32px;
      width: 100px;
      height: 100px;
      border-radius: 50%;
      z-index: 3;
    }
  }
}