/*color*/
.color-gradient{
    background: linear-gradient(#09ACF5, #62DB54); /* - */
    background-clip: text; /*  */
    -webkit-background-clip: text; /*  Chrome  webkit  */
    color: transparent; /*  color: transparent;  */
}
.bg-color-gradient{
    background: linear-gradient(104deg, #09ACF5, #62DB54);
}

